.df-navbar {
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #141414;
   // background-color: rgb(109, 188, 245);
   // box-shadow: 0px 0px 3px 0px rgb(0, 93, 160);
    .df-logo {
        margin-left: 20px;
        font-size: 20px;
        a {
            color: #fff;
        }
    }
    .df-works {
        flex-grow: 1;
        font-size: 20px;
        margin-right: 20px;
        a {
            float:right;
            color: #fff;
        }
    }
}


@media (max-width: 1000px) {
    .df-navbar {
        .df-logo {
            margin-left: 10px;
        }
    }
}