.footer {
    width: 100%;
    min-height: 40px;
    background-color: #c3c3c3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    p {
        margin-left: 0;
        font-weight: bold;
    }

    img {
        display: block;
        max-width:230px;
        max-height:95px;
        width: auto;
        height: auto;
    }
}

@media screen and (max-width: 576px) {
    .footer { 
        p {
            text-align: center;
        } 
    }
}

@media screen and (min-width: 577px) {
    .footer {
        p {
            margin-left: 20px;
        }
    }
}